import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BaseUrl } from 'src/app/enums/baseUrl';
import { Observable } from 'rxjs';
import { ProductListDto } from 'src/app/models/ProductListDto';
import { IRequest } from 'src/app/models/Request';


@Injectable({
  providedIn: 'root'
})
export class ShoppingService {

  constructor(
    private http: HttpClient
  ) { }


  /**
     * @param filter (optional) 
     * @param tenantId (optional) 
     * @param categoryId (optional) 
     * @param minPrice (optional) 
     * @param maxPrice (optional) 
     * @param hasOffer (optional) 
     * @return Success
     */
    getAllProducts(filter: string | null | undefined, tenantId: number | null | undefined, categoryId: number | null | undefined, minPrice: number | null | undefined, maxPrice: number | null | undefined, hasOffer: boolean | null | undefined): Observable<IRequest<ProductListDto>> {
        let url_ = BaseUrl.baseUrl + "/api/services/app/Request/GetAllProducts?";
        if (filter !== undefined)
            url_ += "Filter=" + encodeURIComponent("" + filter) + "&"; 
        if (tenantId !== undefined)
            url_ += "TenantId=" + encodeURIComponent("" + tenantId) + "&"; 
        if (categoryId !== undefined)
            url_ += "CategoryId=" + encodeURIComponent("" + categoryId) + "&"; 
        if (minPrice !== undefined)
            url_ += "MinPrice=" + encodeURIComponent("" + minPrice) + "&"; 
        if (maxPrice !== undefined)
            url_ += "MaxPrice=" + encodeURIComponent("" + maxPrice) + "&"; 
        if (hasOffer !== undefined)
            url_ += "HasOffer=" + encodeURIComponent("" + hasOffer) + "&"; 
        url_ = url_.replace(/[?&]$/, "");

        return this.http.get<any>(url_);
    }

  }